import {mapState} from 'vuex'
export default {
	data() {
		return {
			tgText:'',
			loaddown:false
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key',
			'name'
		])
	},
	methods: {
		getText:function(){
			var _this = this
			this.loaddown = false
			console.log(this.requesturl)
			uni.request({
				url:_this.requesturl + '/txapi/tiangou/index?key=' + _this.key,
				success:(data) => {
					console.log(data)
					var tgText = data.data.newslist[0].content;
					_this.tgText = tgText
					_this.loaddown = true
				}
			})
		},
		refreshitem:function(){
			this.tgText = '';
			this.getText()
		}
	},
	mounted(){
		this.getText()
	}
}
